<template>
  <!-- <div v-if="dialogOpen" class="dialog-base" @click="closeDialog">
    <dialog open>
      <div class="dialog-X">
        <img src="../../assets/close.svg" @click="closeDialog" />
      </div>
      <span class="helper"></span>
      <img :src="theImg" alt="..." @click.stop="showDialog" />
    </dialog>
  </div> -->

  <img-dialog :theImg="theImg" :dialogOpen="dialogOpen" @close-dialog="closeDialog"></img-dialog>
  <div class="main-img" :class="side === 'right' ? 'f-right' : 'f-left'">
    <img :src="theImg.url" alt="" @click="showDialog"  @error="imageLoadError"/>
  </div>
</template>

<script>
import ImgDialog from './ImgDialog.vue'
export default {
  components: {
    ImgDialog
  },
  props: ['side', 'theImg'],
  data () {
    return {
      dialogOpen: false
    }
  },
  computed: {},
  methods: {
    showDialog () {
      this.dialogOpen = true
      this.$store.state.showDialog = true
      //这里不能直接用 $store.state.showDialog来控制 dialog的显示,
      //因为我有20多个img, 有20多个 dialog, 而 store只有一个 boolean, 入如果用它来控制,
      //一下就会显示20多个出来
      //而本地的dialogOpen则只作用于当前的img
      // document.documentElement.style.overflow = 'hidden'
      // console.log('show')
    },
    closeDialog () {
      this.dialogOpen = false
      this.$store.state.showDialog = false
      // document.documentElement.style.overflow = 'auto'
    },
    imageLoadError(){
      // console.log("url invalid");
    }
  }
}
</script>

<style scoped>
.main-img {
  /* background-color: #fff; */
  width: 49%;
}
.main-img img {
  cursor: pointer;
}

/* .main-img img {
  max-height: 100%;
  width: auto;
  max-width: 100%;
  height: auto;
} */
.f-left {
  float: left;
  height: 100%;
}
.f-right {
  text-align: center;
  height: 100%;
  float: right;
}
.dialog-base {
  display: table-cell;
  position: fixed;
  top: 0vh;
  width: 100vw;
  left: 0px;
  height: 100vh;
  overflow: hidden;
  text-align: center;
  vertical-align: middle;

  z-index: 1001;
}
.dialog-X {
  position: absolute;
  right: 2%;
  top: 2%;
  color: white;
  cursor: pointer;
  width: 2%;
}
dialog {
  width: 100%;
  height: 100%;
  border: none;
  display: table-cell;
  /* padding: 2% 5% 2% 5%; */
  /* padding-top: 2%; */
  vertical-align: middle;
  background-color: rgba(0, 0, 0, 0.164);
}
.helper {
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
dialog img {
  vertical-align: middle;
}
img {
  max-height: 100%;
  width: auto;
  max-width: 100%;
  height: auto;
}
</style>
