<template>
  <top-card title="BIO"></top-card>
  <!-- <horizontal-img></horizontal-img> -->
  <vertical-img :theImg="imgmeta[0]"></vertical-img>
  <play-ground :theImg="imgmeta[1]"></play-ground>
</template>

<script>
import TopCard from '../../../basic/TopCard.vue'
import PlayGround from './PlayGround.vue'
// import HorizontalImg from './HorizontalImg.vue';
import VerticalImg from './VerticalImg.vue'
export default {
  components: {
    TopCard,
    // HorizontalImg,
    VerticalImg,
    PlayGround
  },
  data () {
    return {
      imgmeta: {}
    }
  },
  created () {
    const Httpreq = new XMLHttpRequest() // a new request
    Httpreq.open(
      'GET',
      'https://raw.githubusercontent.com/tonystevenj/stevenj-studio-photo/master/bio/imgmeta.json',
      false
    )
    Httpreq.send(null)
    this.imgmeta = JSON.parse(Httpreq.responseText)
  }
}
</script>

