<template>
  <div class='main-pinned'>
        <base-row v-for="i in Math.floor(imgmeta.length/2)" :key="i" :imagePair="[imgmeta[2*(i-1)],imgmeta[2*(i-1)+1]]">
    <!-- {{images.length}} + {{2*(i-1)}} + {{2*(i-1)+1}} -->
    </base-row>
    <!-- <base-row :imagePair="[images[2],images[3]]"></base-row> -->
  </div>
</template>

<script>
import BaseRow from "../../../basic/BaseRow.vue";
export default {
  components: { BaseRow },
    created () {
    const Httpreq = new XMLHttpRequest() // a new request
    Httpreq.open(
      'GET',
      'https://raw.githubusercontent.com/tonystevenj/stevenj-studio-photo/master/portfolio/imgmeta.json',
      false
      // true
    )
    Httpreq.send(null)
    // console.log(Httpreq.responseText);
    this.imgmeta = JSON.parse(Httpreq.responseText)
    this.imgmeta = this.imgmeta.filter(img => img.pinned==="true");
    // console.log(this.imgmeta);
  },
  data() {
    return {
      imgmeta:[],
      // images: [
      //   "https://raw.githubusercontent.com/tonystevenj/stevenj-studio-photo/master/pinned/1.jpg",
      //   "https://raw.githubusercontent.com/tonystevenj/stevenj-studio-photo/master/pinned/5.jpg",
      //   "https://raw.githubusercontent.com/tonystevenj/stevenj-studio-photo/master/pinned/7.jpg",
      //   "https://raw.githubusercontent.com/tonystevenj/stevenj-studio-photo/master/pinned/IMG_3986.jpg",
      //   "https://raw.githubusercontent.com/tonystevenj/stevenj-studio-photo/master/pinned/IMG_6653.jpg",
      //   "https://raw.githubusercontent.com/tonystevenj/stevenj-studio-photo/master/pinned/IMG_3791.jpg",

      //   // require("../../../../assets/images/pinned/1.jpg"),
      //   // require("../../../../assets/images/pinned/5.jpg"),
      //   // require("../../../../assets/images/pinned/7.jpg"),
      //   // require("../../../../assets/images/pinned/IMG_3791.jpg"),
      //   // require("../../../../assets/images/pinned/IMG_3986.jpg"),
      //   // require("../../../../assets/images/pinned/IMG_6653.jpg"),
      // ],
    };
  },
};
</script>

